// wxapiapiprimise
export const loginWxApi = () => {
    return new Promise((resolve, reject) => {
        uni.login({
            timeout: 1000,
            success: (res) => {
                //code
                const code = res.code; //

                resolve(code);
            },
            fail: (err) => {
                console.log(err);
                reject(err);
            }
        });
    });
};
