export const get = (requestUrl) => {
    //wx.showLoading titleloading
    uni.showLoading({
        title: ''
    }); //promise

    return new Promise((resolve, reject) => {
        //Wx.request5,url method,success(),fail(),compete()
        uni.request({
            url: baseUrl + requestUrl,
            method: 'GET',
            header: {
                authorization: 'Bearer ' + uni.getStorageSync('JK-token'),
                'content-type': 'application/json;charset=utf-8',
                Accpet: 'application/json'
            },
            dataType: 'json',
            success: (res) => {
                //resolve(res) =resresolveres
                //resolvethen
                resolve(res);
            },
            fail: reject,
            //
            complete: () => {
                uni.hideLoading();
            }
        });
    }); //get
};
export const getWithParams = (requestUrl, data) => {
    //wx.showLoading titleloading
    uni.showLoading({
        title: ''
    }); //promise

    return new Promise((resolve, reject) => {
        //Wx.request5,url method,success(),fail(),compete()
        uni.request({
            url: baseUrl + requestUrl,
            method: 'GET',
            data: data,
            header: {
                authorization: 'Bearer ' + uni.getStorageSync('JK-token'),
                'content-type': 'application/json;charset=utf-8',
                Accpet: 'application/json'
            },
            dataType: 'json',
            success: (res) => {
                //resolve(res) =resresolveres
                //resolvethen
                resolve(res);
            },
            fail: (res) => {
                if (!uni.getStorageSync('JK-token') || res.statusCode !== 200) {
                    uni.navigateTo({
                        url: '/pages/login/index'
                    });
                }

                reject;
            },
            //
            complete: () => {
                uni.hideLoading();
            }
        });
    }); //get
};
