export const post = (requestUrl,data)=> {
    //wx.showLoading titleloading
    uni.showLoading({
        title: ''
    }); //promise

    return new Promise((resolve, reject) => {
        //Wx.request5,url method,success(),fail(),compete()
        uni.request({
            url: baseUrl + requestUrl,
            method: 'POST',
            //
            data: data,
            success: (res) => {
                //401tokenremovetoken
                if (res.statusCode === 401) {
                    removeToken(); // getCurrentPages() 
                    // 
                    // 0: XA {__wxExparserNodeId__: "e6a15a80", __route__: "pages/login/index", route: "pages/login/index",
                    // 1: XA {__wxExparserNodeId__: "2b6e3cf9", __route__: "pages/login/index", route: "pages/login/index", __displayReporter: B, onLogin: , }
                    // length: 2

                    const currentPages = getCurrentPages(); //login

                    const currentRoute = currentPages[currentPages.length - 1].route;

                    if (currentRoute != 'pages/login/index') {
                        uni.navigateTo({
                            //navigateTo  urlpage '/'
                            //redirect redirect
                            url: `/pages/login/index?redirect=${currentRoute}`
                        });
                    } //

                    uni.showToast({
                        title: '',
                        icon: 'error'
                    });
                } // console.log(res.header['authorization'])
                //token

                _handleToken(res.header); //resolve(res) =resresolveres
                //resolvethen

                resolve(res.data);
            },
            fail: reject,
            //
            complete: () => {
                uni.hideLoading();
            }
        });
    }); //post
}; //token

const _handleToken = (header) => {
    //headerAuthorization
    const token = header['authorization'] || null; //token
    //localstoragetokenheaderAuthorization

    if (token && getToken() !== token) {
        //tokenlocalstoragetoken
        setToken(token); //

        uni.showToast({
            title: ''
        }); // wx.navigateBack()

        uni.navigateBack();
    }
};
